import React, { useEffect } from 'react';
import Page from 'components/Layout/Page';
import PostCard from 'src/components/Cards/PostCard';
import { Grid, Container } from '@material-ui/core';
import { useAllPosts } from 'hooks/posts/useAllPosts';
import { logger } from '@lubycon/logger';

const blogsPageLogger = logger.getPageLogger('blog_list_page');

const Blogs = () => {
  const posts = useAllPosts();

  useEffect(() => {
    blogsPageLogger.view();
  }, []);

  return (
    <Page pageName="블로그 포스팅" description="루비콘 멤버들이 작성한 블로그 포스팅이에요" showTitle={true}>
      <Container>
        <Grid container spacing={2}>
          {posts.map((post, index) => {
            return (
              <Grid item key={index} xs={12} sm={6} md={4} lg={3}>
                <PostCard
                  post={post}
                  onClick={targetLink => {
                    blogsPageLogger.click('post_card', {
                      targetLink,
                    });
                  }}
                />
              </Grid>
            );
          })}
        </Grid>
      </Container>
    </Page>
  );
};

export default Blogs;
